.repo {
    display: flex;
    flex-direction: column;
    background-color: #24292e;
    width: 300px;
    height: 100px;
    padding: 2rem;
}

.repo:hover {
    box-shadow: 10px 10px 5px 0px rgba(27, 27, 27, 0.75);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(27, 27, 27, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(27, 27, 27, 0.75);
}

.repo > p {
    margin: 0;
}

.repoName {
    font-size: large;
}

.reposList {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}

.stats {
    justify-self: flex-end;
    display: flex;
    justify-content: space-between;
}